import React,{Component} from 'react';
import SearchBar from 'material-ui-search-bar';
import $ from 'jquery';
import './search.css';


class Search extends Component {
  constructor(props) {
    super(props);
    this.state = {
      questions:[],
      dataSource:[]
    }
  }
  getQuestions = (searchText) => {
    $.ajax({
        url: 'http:localhost:3000/questions/view' + searchText,
        type: 'GET',
        dataType: 'JSON',
        success: function(data) {
                let questionsTitles = data.results.map((data) => {
                    return data.title;
                });
                this.setState({dataSource: questionsTitles});
                this.setState({questions: data.results});
        }.bind(this)
    });
}

  render() {
    return(
      <div > 
        <div  className='Search-bar'>
        <SearchBar 
          dataSource={this.state.dataSource}
          onChange={(value) => this.getQuestions(value) }
          onRequestSearch={(value)=> this.getQuestions(value)} /> </div>
      </div> 
    )
   }
}

export default Search;




